@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@100&display=swap);
body {
 font-family: "Heebo", sans-serif;
 margin: 0;
 padding: 0;
 background-color: rgb(38, 38, 38);
 display: flex;
 justify-content: center;
 align-items: center;
 width: 100%;
 height: 100vh;
}

.app-container {
 display: flex;
}

.board {
 position: relative;
 width: 500px;
 height: 500px;
 background-color: rgb(106, 106, 106);
 border-radius: 10px;
 box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
  rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}

.random-object {
 width: 30px;
 height: 30px;
 background-color: brown;
 border-radius: 10px;
 position: absolute;
 cursor: -webkit-grab;
 cursor: grab;
}

.stats {
 height: 300px;
 width: 150px;
 background-color: rgb(165, 165, 165);
 border-radius: 10px;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 font-size: 40px;
}

.timer-title {
 font-size: 15px;
}

.basket {
 width: 100px;
 height: 100px;
 background-color: burlywood;
 border-radius: 10px;
 margin: 0 40px;
 display: flex;
 justify-content: center;
 align-items: center;
}

